html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper {
  max-width: 970px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  width: 120px;
  border: 1px solid #00e0d0;
  color: #00e0d0;
  font-family: Hammersmith One, sans-serif;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  padding: 12px 0;
}

.btn:hover {
  background-color: #00e0d0;
  color: white;
}

.logo {
  display: flex;
  align-items: center;
}

.logo span {
  background-image: linear-gradient(to right, #00e0d0 0%, #4aa3cc 100%);
  background-clip: text;
  color: transparent;
  font-size: 30px;
  font-weight: 100;
  padding-left: 11px;
}

.mainScreen {
  min-height: 100vh;
  background-image: linear-gradient(to right, rgba(9, 5, 47, 0.85) 0%, rgba(49, 29, 94, 0.85) 100%), url("../img/mainScreen/main_bg.png");
  background-size: cover;
}

.mainScreen__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 15px 0;
  align-items: center;
  font-family: Roboto, sans-serif;
}

.mainScreen__header--nav ul {
  display: flex;
  flex-wrap: wrap;
}

.mainScreen__header--nav ul li {
  padding: 10px 0 10px 25px;
}

.mainScreen__header--nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.33px;
}

.mainScreen__header--nav ul li a:hover {
  color: #00e0d0;
}

.mainScreen__sliders {
  color: white;
  margin: 0 auto;
  max-width: 770px;
  text-align: center;
  padding-top: 120px;
}

.mainScreen__sliders h1 {
  color: #ffffff;
  font-family: Playfair Display, sans-serif;
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 0.32px;
  padding-bottom: 21px;
}

.mainScreen__sliders hr {
  width: 50px;
  height: 1px;
  color: #00e0d0;
  margin: 0 auto;
}

.mainScreen__sliders p {
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0.7px;
  padding: 30px 10px 50px;
}

.mainScreen__sliders .btn {
  margin: 0 auto;
}

.slick-dots {
  position: static;
  padding: 130px 0 30px;
}

.slick-dots li {
  width: 10px;
  height: 10px;
  border: 1px solid #c2c2c2;
  border-radius: 50%;
}

.slick-dots li button {
  display: none;
}

.slick-dots .slick-active {
  background-color: #00e0d0;
}

#scrollTop {
  transition: all 0.3s linear;
  position: fixed;
  right: 10px;
  bottom: -100px;
  font-size: 50px;
  color: #00e0d0;
  cursor: pointer;
  background-color: #09052f;
  opacity: 0.7;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-decoration: none;
  text-align: center;
  padding-top: 7px;
  z-index: 12;
}

#scrollTop:hover {
  color: #09052f;
  background-color: #00e0d0;
}

.mainScreen__header--navMobile {
  display: none;
}

.mainScreen__header--navMobile .hamb {
  width: 37px;
  height: 37px;
  cursor: pointer;
  position: relative;
  left: -15px;
  z-index: 99;
}

.mainScreen__header--navMobile .hamb span {
  display: inline-block;
  width: 100%;
  border-top: 2px solid #00e0d0;
  position: relative;
  transition: all 0.3s linear;
}

.mainScreen__header--navMobile .hamb span:before,
.mainScreen__header--navMobile .hamb span:after {
  transition: all 0.3s linear;
  content: "";
  border-top: 2px solid #00e0d0;
  position: absolute;
  width: 100%;
}

.mainScreen__header--navMobile .hamb span:before {
  top: -12px;
}

.mainScreen__header--navMobile .hamb span:after {
  top: 8px;
}

.mainScreen__header--navMobile .hamb__active {
  position: fixed;
  left: unset;
  right: 15px;
}

.mainScreen__header--navMobile .hamb__active span {
  border: transparent;
}

.mainScreen__header--navMobile .hamb__active span:before {
  transform: rotate(135deg);
  top: 0;
}

.mainScreen__header--navMobile .hamb__active span:after {
  transform: rotate(-135deg);
  top: 0;
}

.mainScreen__header--navMobile .overlay {
  position: fixed;
  height: 100vh;
  top: 0;
  left: -50%;
  width: 50%;
  background-image: linear-gradient(to right, #311d5e 0%, #09052f 100%);
  z-index: 7;
  transition: all 0.3s linear;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mainScreen__header--navMobile .overlay ul li {
  text-align: center;
  padding: 10px;
}

.mainScreen__header--navMobile .overlay ul li a {
  text-decoration: none;
  color: #00e0d0;
}

.mainScreen__header--navMobile .overlay__active {
  left: 50%;
}

.storyWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 70px 0;
}

.storyWrapper__img {
  flex-basis: 235px;
  margin: 50px 0;
}

.storyWrapper__desc {
  flex-basis: 538px;
}

.storyWrapper__desc h2 {
  color: #413d4b;
  font-family: Playfair Display, sans-serif;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1.1px;
  padding-bottom: 28px;
}

.storyWrapper__desc p {
  opacity: 0.75;
  color: #413d4b;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: 0.42px;
  padding-bottom: 31px;
}

.storyWrapper__desc--btn {
  text-align: center;
}

.watch {
  cursor: pointer;
}

.watch video {
  object-fit: cover;
}

.expertise {
  padding: 62px 0 78px;
  font-family: Playfair Display, sans-serif;
}

.expertise h2 {
  color: #413d4b;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1.1px;
  text-align: center;
}

.expertise p {
  text-align: center;
  padding: 17px 0;
  opacity: 0.75;
  color: #2f2d35;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1.05px;
}

.expertise hr {
  width: 50px;
  height: 2px;
  background-color: #413d4b;
  opacity: 0.3;
  margin: 0 auto;
}

.expertise .expertiseWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 48px;
}

.expertise .expertiseWrapper__item {
  padding: 39px 0;
  max-width: 310px;
  text-align: center;
  font-family: Roboto, sans-serif;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.expertise .expertiseWrapper__item:nth-child(-n+3) {
  border-top: none;
}

.expertise .expertiseWrapper__item:nth-child(1),
.expertise .expertiseWrapper__item:nth-child(4) {
  border-left: none;
}

.expertise .expertiseWrapper__item:hover > img {
  transition: all 0.5s linear;
  transform: scale(1.5);
}

.expertise .expertiseWrapper h3 {
  padding: 35px 0 25px;
  color: #413d4b;
  font-size: 14px;
  font-weight: 900;
  letter-spacing: 0.7px;
}

.expertise .expertiseWrapper p {
  opacity: 0.75;
  color: #333333;
  font-size: 14px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: 0.7px;
  padding: 0 25px;
}

.ourTeam {
  background-image: linear-gradient(to right, rgba(9, 5, 47, 0.85) 0%, rgba(49, 29, 94, 0.85) 100%), url("../img/ourTeam/ourTeam_bg.png");
  background-size: cover;
}

.teamWrapper {
  padding: 70px 0;
  color: white;
  text-align: center;
  font-family: Playfair Display, sans-serif;
}

.teamWrapper h2 {
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1.1px;
}

.teamWrapper p {
  color: #f9f9f9;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1.05px;
  padding: 16px 0 17px;
}

.teamWrapper hr {
  width: 50px;
  height: 2px;
  color: white;
  margin: 0 auto;
  opacity: 0.2;
  margin-bottom: 47px;
}

.teamWrapper__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.teamWrapper__items--item {
  border: 1px solid rgba(255, 255, 255, 0.4);
  max-width: 195px;
  margin: 10px;
  font-family: Roboto, sans-serif;
  cursor: pointer;
  overflow: hidden;
}

.teamWrapper__items--item img {
  transition: all 0.5s linear;
}

.teamWrapper__items--item img:hover {
  transform: scale(1.9);
}

.teamWrapper__items--item h2 {
  font-size: 14px;
  font-weight: 900;
  letter-spacing: 0.7px;
  padding-top: 23px;
  opacity: 0.9;
}

.teamWrapper__items--item p {
  opacity: 0.7;
  color: #fafafa;
  font-size: 13px;
  font-weight: 300;
  padding: 12px 0 25px;
}

.teamWrapper h3 {
  color: #f3f3f3;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.7px;
  padding: 70px 0 26px;
}

.teamWrapper__btn {
  margin: 0 auto;
  width: 140px;
}

.workWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.workWrapper h2 {
  color: #413d4b;
  font-family: Playfair Display, sans-serif;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1.1px;
  padding: 70px 0;
}

.workWrapper a {
  text-decoration: none;
  color: #413d4b;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.7px;
}

.ourWorks__portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.ourWorks__portfolio img {
  width: 100%;
  height: 100%;
  cursor: pointer;
  max-width: 225px;
}

.ourWorks__btn {
  margin: 70px auto;
  text-align: center;
}

.feedback {
  background-image: linear-gradient(to right, rgba(9, 5, 47, 0.85) 0%, rgba(49, 29, 94, 0.85) 100%), url("../img/feedback/feedback_bg.png");
  background-size: cover;
  padding: 70px 0;
}

.sayWrapper {
  color: #ffffff;
  text-align: center;
  font-family: Playfair Display, sans-serif;
}

.sayWrapper span {
  opacity: 0.9;
  font-size: 100px;
  font-weight: 400;
  letter-spacing: 3px;
}

.sayWrapper p {
  margin: 0 auto;
  max-width: 770px;
  opacity: 0.85;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0.4px;
}

.sayWrapper h2 {
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 900;
  line-height: 22px;
  letter-spacing: 0.7px;
  padding: 52px 0 12px;
}

.sayWrapper h3 {
  opacity: 0.7;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 300;
  line-height: 22px;
}

.sayWrapper-nav {
  max-width: 270px;
  height: 65px;
  margin: 20px auto 0;
}

.sayWrapper-nav_m {
  width: 45px !important;
  height: 45px !important;
  margin: 5px;
}

.contact {
  padding: 71px 0 51px;
}

.contactWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.contactWrapper__form {
  max-width: 460px;
  width: 100%;
}

.contactWrapper__form h2 {
  color: #413d4b;
  font-family: Playfair Display, sans-serif;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1.1px;
  padding-bottom: 35px;
}

.contactWrapper__form form input:not([type="submit"]),
.contactWrapper__form form textarea {
  width: 100%;
  padding: 14px 0 14px 21px;
  margin: 10px 0;
  opacity: 0.7;
  color: #413d4b;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.7px;
}

.contactWrapper__form form input[type="submit"] {
  display: block;
  margin-bottom: 30px;
}

.contactWrapper__clients {
  max-width: 381px;
}

.contactWrapper__clients h2 {
  color: #413d4b;
  font-family: Playfair Display, sans-serif;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1.1px;
  padding-bottom: 37px;
}

.contactWrapper__clients--client {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.contactWrapper__clients--client img {
  min-width: 140px;
  margin: 20px 5px;
  transition: all 0.5s linear;
}

.contactWrapper__clients--client img:hover {
  transform: scale(1.2);
}

.footer {
  background-image: linear-gradient(to right, rgba(9, 5, 47, 0.85) 0%, rgba(49, 29, 94, 0.85) 100%), url("../img/footer/footer_bg.png");
  background-size: cover;
  padding: 70px 0 50px;
  font-family: Roboto, sans-serif;
}

.footer__firstLine {
  display: flex;
  flex-wrap: wrap;
  color: white;
  justify-content: space-between;
}

.footer__firstLine--desc {
  max-width: 283px;
}

.footer__firstLine--desc p {
  font-size: 14px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 0.35px;
  padding-top: 37px;
}

.footer__firstLine--address {
  max-width: 267px;
  margin: 0 30px;
}

.footer__firstLine--address h2 {
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.75px;
  padding-top: 29px;
}

.footer__firstLine--address p {
  font-size: 14px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 0.35px;
  padding: 42px 0 27px 20px;
  position: relative;
}

.footer__firstLine--address span {
  display: inline-block;
  position: relative;
  padding-left: 20px;
  font-size: 14px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 0.35px;
}

.footer__firstLine--address .icon {
  position: absolute;
  left: -10px;
  font-size: 20px;
}

.footer__firstLine--sub h2 {
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.75px;
  padding: 29px 0 39px;
}

.footer__firstLine--sub form input[type="email"] {
  background-color: transparent;
  width: 211px;
  height: 45px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  color: white;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.7px;
  text-align: center;
}

.footer__firstLine--sub form button {
  width: 70px;
  height: 45px;
  border: none;
  background-color: #ffffff;
  opacity: 0.5;
  cursor: pointer;
  position: relative;
  top: 6px;
}

.footer__firstLine--sub form button i {
  font-size: 30px;
}

.footer__firstLine--sub .social {
  padding-top: 27px;
  display: flex;
}

.footer__firstLine--sub .social a {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  width: 35px;
  height: 35px;
  border: 1px solid white;
  border-radius: 50%;
  color: white;
  margin: 8px;
}

.footer__firstLine--sub .social a:first-child {
  margin-left: 0;
}

.footer__secondLine {
  padding-top: 71px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__secondLine--support {
  padding-bottom: 20px;
}

.footer__secondLine--support a {
  text-decoration: none;
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.35px;
  padding-left: 30px;
}

.footer__secondLine--support a:first-child {
  padding-left: 0;
}

.footer__secondLine--copy {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.35px;
}

@media screen and (max-width: 990px) {
  .expertise .expertiseWrapper__item {
    border: 1px solid rgba(0, 0, 0, 0.1) !important;
    margin: 5px;
  }

  .footer {
    text-align: center;
  }

  .footer__firstLine {
    justify-content: space-around;
  }

  .footer__firstLine--logo {
    justify-content: center;
  }

  .footer__secondLine {
    justify-content: space-around;
  }
}

@media screen and (max-width: 885px) {
  .contactWrapper__clients h2 {
    text-align: center;
  }

  .contactWrapper__clients--client {
    justify-content: space-around;
  }

  .contactWrapper__form form input[type="submit"] {
    margin: 5px auto 30px;
  }
}

@media screen and (max-width: 755px) {
  .mainScreen__header--nav {
    display: none;
  }

  .mainScreen__header--navMobile {
    display: block;
  }
}

@media screen and (max-width: 654px) {
  .footer__firstLine--address {
    margin-top: 50px;
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 430px) {
  .mainScreen__sliders {
    padding-top: 50px;
  }

  .slick-dots {
    padding-top: 50px;
  }

  .storyWrapper {
    text-align: center;
  }

  .storyWrapper__img {
    margin-top: 0;
  }

  .storyWrapper__desc--btn {
    margin: 0 auto;
  }

  .mainScreen__header--navMobile .overlay {
    width: 100%;
    left: -100%;
  }

  .mainScreen__header--navMobile .overlay__active {
    left: 0;
  }

  .slick-prev.slick-arrow,
  .slick-next.slick-arrow {
    display: none;
  }
}